@import url("https://fonts.googleapis.com/css2?family=Wix+Madefor+Text:ital,wght@0,400..800;1,400..800&family=Work+Sans:ital,wght@0,100..900;1,100..900&display=swap");
@font-face {
  font-family: "Proxima Nova";
  src: url("../fonts/proximanova_regular.ttf");
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  display: block;
  max-width: 100%;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: "Proxima Nova", sans-serif;
  font-weight: 400;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #8d8b8b;
  display: flex;
  justify-content: center;
}

main {
  margin-top: 50px;
}

.card {
  max-width: 744px;
  max-height: 100%;
  background-color: #fff;
  border-radius: 8px;
}

.card-wrapper {
  padding: 24px 24px 0 24px;
}

.card__header {
  border-bottom: 1px solid rgb(201, 201, 201);
}

.player {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 23px;
}

.player__info {
  display: flex;
  gap: 12px;
}

.player__info__avatar {
  max-width: 32px;
  max-height: 32px;
  border-radius: 50%;
}

.name {
  color: rgba(0, 0, 0, 0.8);
}

.description {
  font-weight: 400;
  color: rgba(0, 0, 0, 0.4);
}

table {
  border-collapse: separate;
  border-spacing: 12px 0;
}

table span {
  display: none;
}

th,
td {
  width: 64px;
  text-align: start;
  font-size: 14px;
}

th {
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

td {
  font-weight: bold;
}

.plus,
#plus__value {
  color: #62b435;
}

.equal,
#equal__value {
  color: rgba(0, 0, 0, 0.4);
}

.minus,
#minus__value {
  color: #f64747;
}

.roi,
.profit {
  font-size: 12px;
  color: rgba(0, 0, 0, 0.8);
  font-weight: 400;
}

.roi {
  text-transform: uppercase;
}

#roi__value {
  color: #62b435;
}

#profit__value {
  color: #62b435;
}

.card__body {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 20px;
  color: rgba(0, 0, 0, 0.6);
  font-size: 16px;
}

.card__text {
  height: 99px;
  overflow: hidden;
  position: relative;
  transition: all 0.5s;
}
.card__text:after {
  content: "";
  position: absolute;
  width: 100%;
  height: 20px;
  background: linear-gradient(to top, white, rgba(0, 0, 255, 0));
  left: 0px;
  bottom: 0px;
}

.card__text--full {
  height: 160px;
  transition: all 0.5s;
}
.card__text--full:after {
  display: none;
}

.card__button {
  position: relative;
  top: 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background-color: white;
  border: none;
  box-shadow: 0px 3px 6px 0px #c2c1c1;
  width: 40px;
  height: 40px;
  cursor: pointer;
  transition: all 0.5s;
}

.card__button__arrow {
  transition: all 0.5s;
}
.card__button__arrow--rotate {
  transform: rotate(180deg);
}

.card__footer {
  padding-bottom: 20px;
}

.bet {
  display: flex;
  justify-content: space-between;
  padding: 24px;
  border-left: 3px solid #3498db;
  flex-wrap: wrap;
  background-color: #eff0f0;
}

.bet__title {
  width: 400px;
}

.bet__title__forecast {
  text-transform: uppercase;
  font-size: 12px;
  color: rgba(0, 0, 0, 0.4);
  font-weight: 600;
  padding-bottom: 4px;
}

.bet__title__description {
  font-size: 18px;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.8);
}

.bet__info {
  display: flex;
  align-items: center;
}

.bet__logo {
  margin-right: 12px;
  max-width: 32px;
}

.bet__ratio {
  margin-right: 16px;
}

.bet__ratio__title {
  font-size: 12px;
  color: rgba(0, 0, 0, 0.8);
  font-weight: 400;
}
.bet__ratio__title .short {
  display: none;
}

.bet__ratio__value {
  font-size: 20px;
  color: rgba(0, 0, 0, 0.8);
  font-weight: 600;
}

.bet__button {
  width: 140px;
  height: 40px;
  border-radius: 4px;
  border: none;
  background-color: #3498db;
  color: white;
  font-size: 14px;
  font-weight: 600;
  font-family: "Wix Madefor Text";
  cursor: pointer;
  position: relative;
  transition: all 0.5s;
  overflow: hidden;
}
.bet__button::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(1, 255, 1, 0.5);
  transform: translateX(-100%);
  transition: all 0.5s;
}
.bet__button:hover::before {
  transform: translateX(0);
}
.bet__button:hover {
  background: none;
  color: black;
}

@media (max-width: 743px) {
  .card {
    width: 351px;
  }
  .card__text--full {
    height: 370px;
  }
  .description span {
    display: none;
  }
  table {
    width: 86%;
  }
  thead {
    display: none;
  }
  td span {
    color: rgba(0, 0, 0, 0.4);
  }
  table {
    border-collapse: collapse;
  }
  #plus__value {
    width: 35px;
  }
  #equal__value {
    width: 30px;
  }
  #minus__value {
    width: 35px;
  }
  #roi__value {
    width: 106px;
    color: rgba(0, 0, 0, 0.8);
  }
  #roi__value span {
    color: rgba(0, 0, 0, 0.8);
  }
  #profit__value {
    width: 54px;
  }
  table span {
    display: inline;
  }
  .player {
    flex-direction: column;
    align-items: stretch;
    padding-bottom: 14px;
  }
  .player__info__text {
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
  .player__stats {
    position: relative;
    top: -13px;
    display: flex;
    justify-content: end;
    width: 100%;
  }
  .bet__title {
    width: 190px;
  }
  .bet__title__description {
    font-size: 16px;
  }
  .bet__ratio__title .full {
    display: none;
  }
  .bet__ratio__title .short {
    display: inline;
  }
  .bet__info {
    align-items: flex-end;
  }
  .bet__ratio {
    margin-right: 0;
  }
  .bet__logo {
    padding-bottom: 3px;
  }
  .bet__ratio__value {
    font-size: 18px;
  }
  .bet__button {
    margin-top: 16px;
    width: 100%;
  }
}